<template>
  <v-container>
    <v-form @submit.prevent="submitForm">
      <v-row>
        <!-- Student Name -->
        <v-col cols="12" md="6">
          <v-text-field
            v-model="studentDetails[0].name"
            label="Student Name"
            outlined
            required
          ></v-text-field>
        </v-col>

        <!-- Email -->
        <v-col cols="12" md="6">
          <v-text-field
            v-model="studentDetails[0].email"
            label="Email"
            outlined
            required
            type="email"
          ></v-text-field>
        </v-col>

        <!-- Phone -->
        <v-col cols="12" md="6">
          <v-text-field
            v-model="studentDetails[0].phone"
            label="Phone"
            outlined
            @input="validatePhoneNumber"
            required
          ></v-text-field>
          <v-alert v-if="phoneError" type="error" outlined>{{ phoneError }}</v-alert>
        </v-col>

        <!-- Address -->
        <v-col cols="12" md="6">
          <v-textarea
            v-model="studentDetails[0].address"
            label="Address"
            outlined
            required
          ></v-textarea>
        </v-col>

        <!-- Percentage Obtained -->
        <v-col cols="12" md="6">
          <v-text-field
            v-model="studentDetails[0].percentage"
            label="Percentage Obtained"
            outlined
            required
            type="number"
          ></v-text-field>
        </v-col>

        <!-- Top Class -->
        <v-col cols="12">
          <v-checkbox v-model="studentDetails[0].topClass" label="Top Class"></v-checkbox>
        </v-col>

        <!-- Submit Button -->
        <v-col cols="12">
          <v-btn v-if="shouldShowSubmitButton" type="submit" color="primary">Submit</v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      studentDetails:  [
        {
          name: '',
          email: '',
          phone: '',
          address: '',
          percentage: null,
          topClass: false,
        }
      ],
      phoneError: '',
    };
  },
  computed: {
    shouldShowSubmitButton() {
      return this.studentDetails[0].topClass;
    },
  },
  methods: {
    submitForm() {
      // Validate phone number again before submission
      this.validatePhoneNumber();

      // Check if there's any error
      if (this.phoneError) {
        // Show a popup or handle the error as needed
        alert(this.phoneError);

        // Assign empty value to the phone field
        this.studentDetails[0].phone = '';
        return; // Prevent form submission if there's an error
      }

      // If you want to append a new object on each submission
      this.studentDetails.push({
        name: '',
        email: '',
        phone: '',
        address: '',
        percentage: null,
        topClass: false,
      });

      // Save to localStorage
      this.saveToLocalStorage();
    },
    validatePhoneNumber() {
      const phone = this.studentDetails[0].phone;
      if (phone.length !== 10) {
        this.phoneError = 'Phone number must be 10 digits.';
      } else {
        this.phoneError = '';
      }
    },
    saveToLocalStorage() {
      localStorage.setItem('studentDetails', JSON.stringify(this.studentDetails));
    },
    retrieveFromLocalStorage() {
      const storedData = localStorage.getItem('studentDetails');
      return storedData ? JSON.parse(storedData) : null;
    },
  },
};
</script>

<style scoped>
.error-message {
  color: red;
  margin-top: 5px;
}
</style>
